ul:not([class]) {
  padding: 0; }
ul:not([class]) > li {
    position: relative;
    list-style: none;
    padding-left: 29px; }
ul:not([class]) > li::before {
      content: "";
      position: absolute;
      width: 4px;
      height: 4px;
      top: 0.5em;
      left: 5px;
      border-radius: 50%;
      background-color: #3e3e3e; }
ul:not([class]) > li:nth-child(n + 2) {
      margin-top: 8px; }
.uk-list.uk-list-check > li {
  position: relative;
  padding-left: 29px; }
.uk-list.uk-list-check > li:nth-child(n + 2) {
    margin-top: 8px; }
.uk-list.uk-list-check > li::before {
    display: block;
    position: absolute;
    top: 0;
    left: 5px;
    color: #00a29c; }
ol:not([class]) {
  counter-reset: ol;
  padding: 0; }
ol:not([class]) > li {
    position: relative;
    list-style: none;
    padding-left: 29px; }
ol:not([class]) > li::before {
      counter-increment: ol;
      content: counter(ol) ".";
      position: absolute;
      top: 0;
      left: 5px;
      color: #3e3e3e; }
ol:not([class]) > li:nth-child(n + 2) {
      margin-top: 8px; }
blockquote {
  background-color: rgba(105, 190, 40, 0.05);
  color: #00a29c;
  padding: 16px 24px;
  border-radius: 4px;
  position: relative;
  z-index: 1; }
blockquote::before {
    color: #00a29c;
    content: "\2033";
    opacity: 0.2;
    position: absolute;
    left: 8px;
    top: 9px;
    font-size: 58px;
    font-style: italic;
    line-height: 0.9;
