@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.nav-bar {
  font-size: 16px;
  background: #fff;
  box-shadow: inset 0 -1px 0 0 #e4e4e4;
  position: relative;
  z-index: 20;
  display: flex;
  flex-direction: column;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
  .icon {
    display: none;
    > img {
      margin-right: 10px;
    }
    @media screen and (max-width: 768px) {
      margin-left: auto;
      position: absolute;
      right: 30px;
      top: 10px;
      display: flex;

      img {
        padding-right: 15px;
        border-right: 1px solid #e4e4e4;
      }
    }
  }
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    align-items: center;
    li {
      margin: 8px 32px 8px 0px;
      @media screen and (max-width: 768px) {
        margin: 15px 0px;
      }
      display: block;
      img {
        margin-right: 5px;
      }
    }
    @media screen and (max-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
      max-height: 0px;
      transition: max-height 100ms ease;
      overflow: hidden;
    }
  }
  @media screen and (max-width: 768px) {
    .responsive {
      max-height: 100vh;
    }
  }

  &__top {
    font-size: 14px;
    line-height: 0.8;
    z-index: 2;
    color: $global-inverse-color;
    background: $global-primary-background;
    @media screen and (max-width: 768px) {
      max-height: 0px;
      transition: max-height 100ms ease;
      overflow: hidden;
    }
  }

  &__top-menu {
    justify-content: flex-end;
    a {
      color: $global-inverse-color;
      &:hover {
        text-decoration: none;
      }
    }
  }

  .responsive-order {
    order: 0;
    @media screen and (max-width: 768px) {
      order: -1;
    }
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
    }
    &__search {
      padding: 2px 6px;
      cursor: pointer;
      margin-right: 0 !important;
      > img {
        display: block;
      }
    }
  }

  &__bottom-logo {
    position: relative;

    &__image {
      width: 92px;
      margin: 10px 16px -26px 0;
      @media screen and (max-width: $breakpoint-small-max) {
        width: 60px;
      }
    }
    &__text {
      width: 157px;
      @media screen and (max-width: $breakpoint-small-max) {
        width: 125px;
      }
    }
  }

  &__bottom-menu {
    @media screen and (max-width: 768px) {
      li:last-of-type {
        display: none;
      }
    }
    transition: margin-top 1ms linear !important;
    margin-top: 0px;
    &.responsive {
      margin-top: 30px !important;
    }

    a {
      color: $gray-dark;
      &:hover {
        text-decoration: none;
        color: $brand-primary;
      }
    }
  }
}
