@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.author-info {
  &__post-info {
    padding: 16px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #7f7f7f;
    font-size: 16px;

    @media (max-width: $breakpoint-small-max) {
      font-size: 13px;
      padding: 10px 0;
      flex-wrap: wrap;
    }
    &--bottom {
      border-top: 2px solid $global-border;

      @media (max-width: $breakpoint-small-max) {
        border-width: 1px;
      }
    }
  }
  &__post-info-author {
    display: flex;
    align-items: center;
  }
  &__top-border {
    border-top: 2px solid $global-border;
  }
  &__bottom-border {
    border-bottom: 2px solid $global-border;
  }
  &__post-info-avatar {
    width: 64px;
    height: 64px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 24px 0 0;
    @media (max-width: $breakpoint-small-max) {
      width: 46px;
      height: 46px;
      margin-right: 16px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__post-info-name {
    color: $global-color;
  }
}
