@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.container {
  cursor: pointer;
  position: relative;
  display: flex;

  p {
    margin: 0px;
    margin-right: 10px;
    &.opened {
      display: none;
    }
  }

  .hamburger-container {
    position: relative;
    width: 20px;
    height: 20px;
  }
}
.hamburger-menu {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 20px;
  height: 15px;
  span {
    margin-top: 4px;
  }
  span,
  span:before,
  span:after {
    border-radius: 1px;
    height: 2px;
    width: 20px;
    background: white;
    position: absolute;
    display: block;
    content: "";
    background: black;
  }

  span:before {
    top: -5px;
  }

  span:after {
    bottom: -5px;
  }
}

.hamburger-menu {
  span,
  span:before,
  span:after {
    transition: all 100ms ease-in-out;
  }
}

.hamburger-menu.opened span {
  background-color: transparent;
}

.hamburger-menu.opened span:before,
.hamburger-menu.opened span:after {
  top: 0;
}
.hamburger-menu.opened span:before {
  transform: rotate(45deg);
}
.hamburger-menu.opened span:after {
  transform: rotate(-45deg);
}
