@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.expandable-panel {
  cursor: pointer;
  margin: 2px 0px;

  &__list {
    margin: 0px;
    display: contents;
    cursor: pointer;
    text-align: left;
    margin: 0px;
    list-style: none;
  }
  &__content {
    z-index: 1;
    position: relative;
    font-size: 16px;
    max-height: 0;
    transition: max-height, margin 0.15s ease-out;
    overflow: hidden;
    margin: 0;
    @media (max-width: $breakpoint-xsmall-max) {
      padding: 8px 16px;
    }
  }
  &__block {
    position: relative;
    padding: 13px 32px;
    margin: 4px 0;
    box-shadow: 10px red;
    z-index: 1;
    background: rgba(0, 0, 0, 0.1);
    clip-path: polygon(
      0 0,
      5% 0.8%,
      15% 0,
      25% 0.8%,
      35% 0,
      50% 0.8%,
      65% 0,
      75% 0.8%,
      85% 0,
      95% 0.8%,
      100% 0,
      99.7% 25%,
      100% 50%,
      99.7% 75%,
      100% 100%,
      95% 99.2%,
      85% 100%,
      70% 99.2%,
      60% 100%,
      50% 99.4%,
      35% 100%,
      25% 99.4%,
      15% 100%,
      9% 99.5%,
      0 100%,
      0.3% 75%,
      0 50%,
      0.3% 25%
    );
  }
  &__block::after {
    left: 1px;
    right: 1px;
    top: 1px;
    bottom: 1px;
    background: $global-background;
    position: absolute;
    content: "";
    clip-path: polygon(
      0 0,
      5% 0.8%,
      15% 0,
      25% 0.8%,
      35% 0,
      50% 0.8%,
      65% 0,
      75% 0.8%,
      85% 0,
      95% 0.8%,
      100% 0,
      99.7% 25%,
      100% 50%,
      99.7% 75%,
      100% 100%,
      95% 99.2%,
      85% 100%,
      70% 99.2%,
      60% 100%,
      50% 99.4%,
      35% 100%,
      25% 99.4%,
      15% 100%,
      9% 99.5%,
      0 100%,
      0.3% 75%,
      0 50%,
      0.3% 25%
    );
  }

  &__title {
    position: relative;
    z-index: 1;
    line-height: 2.44;
    font-size: 18px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    color: $brand-primary;
    letter-spacing: -0.5px;
    user-select: none; /* supported by Chrome and Opera */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    @media (max-width: $breakpoint-xsmall-max) {
      padding: 0 16px;
      font-size: 16px;
      line-height: 56px;
    }
    &:after {
      content: "";
      position: absolute;
      right: 0px;
      top: 50%;
      transform: translateY(-50%);
      width: 12px;
      height: 12px;
      background-image: url("../../../../assets/icons/plus.svg");
    }
  }
}
.expandable-panel__block {
  .expandable-panel__content {
    @media (max-width: $breakpoint-xsmall-max) {
      display: none;
    }
  }
  &__open {
    .expandable-panel__content {
      margin: 8px 0;
      max-height: 500px;
      transition: max-height 0.25s ease-in;
      @media (max-width: $breakpoint-xsmall-max) {
        display: block;
      }
    }
    .expandable-panel__title {
      &:after {
        content: "";
        background-image: url("../../../../assets/icons/minus.svg");
      }
    }
  }
}
