  &--featured-slider,
  &--product {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    padding: 32px 16px;
    text-align: center;
    @media (max-width: $breakpoint-small-max) {
      padding: 24px 16px;
    }
    @media (max-width: $breakpoint-small-max) {
      flex-direction: row;
      clip-path: none;
      padding: 16px;
      align-items: center;
      text-align: left;
      box-shadow: none;
    }
    .related-post-card {
      clip-path: polygon(
        0.4% 0.5%,
        10% 0%,
        14% 0%,
        24% 0.5%,
        26% 0.5%,
        35% 0%,
        39% 0%,
        49% 0.5%,
        51% 0.5%,
        60% 0%,
        64% 0%,
        74% 0.5%,
        76% 0.5%,
        85% 0%,
        89% 0%,
        98.2% 0.5%,
