        14% 100%,
        10% 100%,
        0.4% 99.5%,
        0 90%,
        0.4% 66%,
        0 47%,
        0.4% 33%,
        0% 15%,
        0.4% 0.4%
      );
      &__image {
        margin: 16px 0px;
        overflow: hidden;
        height: 130px;
        margin-bottom: 25px;
        @media (max-width: $breakpoint-small-max) {
          height: 100px;
          margin-bottom: 16px;
        }
        @media (max-width: $breakpoint-small-max) {
          margin: 0;
          height: 80px;
        }
        img {
          width: 100% !important;
          height: 100%;
          object-fit: contain;
          margin: 0 auto;
        }
      }
      &__media {
        @media (max-width: $breakpoint-small-max) {
          margin: 0 16px 0 0;
          flex: 0 0 auto;
          width: 30%;
        }
      }
      &__body {
        padding: 0;
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        width: 100%;
      }
      &__title {
        font-size: 14px;
        line-height: 1.57;
        text-transform: uppercase;
        font-weight: 600;
        color: $gray-dark;
        @media (max-width: $breakpoint-xsmall-max) {
          text-overflow: ellipsis;
          white-space: pre-line;
          overflow: hidden;
          width: 100%;
        }
      }
    }
  }
  &--product {
    padding: 16px;
    min-height: inherit;
    box-sizing: border-box;
    .related-post-card {
      &__head {
        display: flex;
        height: 100%;
        flex-direction: column;
        flex: 1 1 auto;
      }
      &__title {
        flex: 1 1 auto;
        height: 60px;
        text-transform: capitalize;
        margin-bottom: 9px;
        @media (max-width: $breakpoint-small-max) {
          margin-bottom: 0px;
          height: auto;
        }
      }
      &__content {
        font-size: 12px;
        color: $gray-light;
      }
    }
  }
  @media (max-width: $breakpoint-small-max) {
    clip-path: none;
  }

  &__body {
    display: block;
    @media (max-width: $breakpoint-small-max) {
      display: flex !important;
      flex-direction: row !important;
    }
  }
}
.related-post-card__shadow::after {
  content: "";
  z-index: -1;
  position: absolute;
  left: -2px;
  right: -2px;
  bottom: -6px;
  top: -1px;
  background: rgba(0, 0, 0, 0.03);
  filter: blur(10px);

  @media (max-width: $breakpoint-small-max) {
    background: transparent;
    filter: none;
    clip-path: none;
  }
}
