@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.related-product-slider {
  padding: 32px 0;
  position: relative;
  @media (min-width: $breakpoint-medium) {
    padding: 72px 0;
  }

  &__foreground {
    position: relative;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    @media (min-width: $breakpoint-small) {
      margin-bottom: 40px;
    }
    @media (min-width: $breakpoint-medium) {
      margin-bottom: 54px;
    }
  }

  &__header-button {
    display: block;
    @media (max-width: 750px) {
      display: none;
    }
  }

  &__slide {
    width: 100%;
    height: 100%;
  }

  &__bottom-button {
    display: none;
    > :first-child {
      width: 100%;
    }
    @media (max-width: 750px) {
      padding-top: 16px;
      display: block;
    }
  }

  &__mobile-container {
    @media (max-width: 750px) {
      display: block;
      box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.04), 0 0px 2px 0 rgba(0, 0, 0, 0.09);
    }
    display: none;
  }
}
