@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.related-posts {
  @media (min-width: $breakpoint-medium) {
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    @media (min-width: $breakpoint-medium) {
      margin-bottom: 26px;
    }
  }
  &__title {
    margin: 0;
    @extend .uk-h4 !optional;
  }
  &__action {
    @media (max-width: $breakpoint-xsmall-max) {
      padding-top: 16px;
    }
  }
  &__list {
    @media (max-width: $breakpoint-medium-max) {
      > div:nth-child(n + 5) {
        display: none;
      }
    }
    @media (max-width: $breakpoint-small-max) {
      > div:nth-child(n + 4) {
        display: none;
      }
    }
  }
  .card {
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
    @media (max-width: $breakpoint-xsmall-max) {
      flex-direction: row;
      padding: 0;
      align-items: center;
      text-align: left;
      box-shadow: none;
      border-bottom: 1px solid $global-border;
    }
    &__media {
      @media (max-width: $breakpoint-xsmall-max) {
        margin: 0 16px 0 0;
        flex: 0 0 auto;
        width: 30%;
      }
    }
    &__image {
      @media (max-width: $breakpoint-xsmall-max) {
        margin: 0;
        height: auto;
        text-align: center;
      }
    }
    &__body {
      @media (max-width: $breakpoint-xsmall-max) {
        padding: 16px 0;
      }
    }
  }
}
