@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.footer {
  padding: 48px 0 0;
  font-size: 16px;
  @media (max-width: $breakpoint-small-max) {
    padding-top: 32px;
  }

  &__top {
    @media (min-width: $breakpoint-small) {
      display: flex;
      align-items: flex-start;
      padding: 0 0 32px;
      border-bottom: 1px solid $global-border;
    }
  }

  &__section {
    flex: 1 1 auto;
    @media (max-width: $breakpoint-xsmall-max) {
      margin-bottom: 32px;
    }
  }

  &__section-title {
    font-weight: 600;
    color: $gray-dark;
    margin-bottom: 20px;
    @media (max-width: $breakpoint-xsmall-max) {
      margin-bottom: 8px;
    }
  }
  &__section-list {
    li {
      margin-bottom: 9px;
      @media (max-width: $breakpoint-xsmall-max) {
        margin-bottom: 6px;
      }
    }
  }

  &__bottom {
    padding: 16px 0;
    @media (min-width: $breakpoint-medium) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    @media (max-width: $breakpoint-xsmall-max) {
      padding: 0 0 32px;
    }
  }

  &__bottom-left {
    @media (min-width: $breakpoint-medium) {
      display: flex;
      align-items: center;
    }
    @media (max-width: $breakpoint-small-max) {
      margin: 0 0 40px;
    }
    @media (max-width: $breakpoint-xsmall-max) {
      ul {
        display: block;
        li {
          padding: 0 0 6px;
        }
      }
    }
  }

  &__logo {
    width: 156px;
    margin-right: 20px;
    @media (max-width: $breakpoint-small-max) {
      margin: 0 auto 40px;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: center;
    li {
      padding: 0 8px;
    }
  }

  &__social {
    text-align: center;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      &::before {
        display: block;
      }
      a {
        color: $gray-light;
        &:hover {
          color: $brand-primary;
          text-decoration: none;
        }
      }
    }
  }
}
