@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.menu-item {
  list-style: none !important;
  &:not(:first-child) {
    margin-top: 16px;
  }
  > a {
    display: flex;
    list-style: none;
    align-items: center;
    font-weight: 600;
    color: $gray-light;
    &.active,
    &:hover {
      color: $brand-primary;
      text-decoration: none;
      svg {
        path {
          fill: $brand-primary;
        }
      }
    }
  }
  svg {
    margin-right: 8px;
    margin-top: -3px;
    path {
      fill: $gray-light;
    }
  }
  &--active {
    > a {
      color: $brand-primary;
    }
    svg {
      path {
        fill: $brand-primary;
      }
    }
  }
}
