@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.opened {
  opacity: 1;
  max-height: 100vh;
}
.closed {
  opacity: 0;
  max-height: 0;
}
.highlighted {
  background-color: transparent;
  font-size: 15px;
  font-weight: bold;
  color: #003f3d;
}
.navbar-search {
  position: absolute;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  transition: 250ms all ease-in-out;
  &__shadow {
    background-color: rgba(0, 0, 0, 0.6);
    width: 100vw;
    height: 100vh;
    position: fixed;
    transition: 250ms all ease-in-out;
    z-index: 1;
    @media (max-width: $breakpoint-xsmall-max) {
      background-color: white;
    }
  }
  &__results {
    width: 100%;
    background-color: #fff;
    box-shadow: inset 0 -1px 0 0 #e4e4e4;
    h3 {
      line-height: 4;
      text-align: center;
      margin: auto;
    }
    &__item {
      padding: 16px;
      flex: 0 1 50% !important;
      line-height: 3;
      flex-wrap: wrap;
      justify-content: flex-start;
      padding: 16px;
      text-align: left;
      display: flex;
      width: 1160px;
      margin: auto;
      background-color: #fff;
      box-shadow: inset 0 -1px 0 0 #e4e4e4;
      h4 {
        flex: 0 1 100% !important;
        margin: 6px 0;
        text-align: left;
      }
      @media (max-width: $breakpoint-medium-max) {
        width: auto;
        margin: 0px 37px;
      }
      &__details {
        flex: 0 1 50% !important;
        @media (max-width: $breakpoint-xsmall-max) {
          flex: 0 1 100% !important;
        }
      }
    }
  }
  &__bar {
    position: relative;
    margin-top: 29px;
    z-index: 2;
    transition: 250ms all ease-in-out;
    background-color: white;
    @media screen and (max-width: 768px) {
      margin-top: 0px;
    }
    &__top {
      font-size: 16px;
      background: #fff;
      box-shadow: inset 0 -1px 0 0 #e4e4e4;
      z-index: 2;
      display: flex;
      flex-direction: column;
      -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
      -khtml-user-select: none; /* Konqueror HTML */
      -moz-user-select: none; /* Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
      user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
      width: 100%;
      height: 73px;
      display: flex;
      padding: 0;
      flex-direction: row-reverse;
      margin: 0;
      list-style-type: none;
      list-style: none;
      text-decoration: none;
      display: flex;
      align-items: center;
      justify-content: space-between;
      > ul li {
        list-style-type: none;
      }
      &__item {
        list-style: none;
        margin: 8px 6px 8px 0px;
        display: table;
        width: 100%;
        @media screen and (max-width: 768px) {
          margin: 15px 0px;
        }
        display: block;
        img {
          margin-right: 5px;
          display: table-cell;
        }
        &__close {
          cursor: pointer;
          display: table-cell;
        }
        &__input {
          border: none;
          font-size: 16px;
          line-height: 2;
          display: table-cell;
          width: 90%;
          margin: 0px 8px;
        }
        &__input:focus {
          outline: none;
          border-bottom: 2 solid $brand-primary;
        }
      }
    }
  }
}
