@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.ontactformulier {
  padding: 30px 0;
  background: $global-muted-background;

  @media (min-width: $breakpoint-medium) {
    padding: 72px 0;
  }

  &__items {
    @media (min-width: $breakpoint-medium) {
      margin-left: -24px;
      display: flex;
      flex-wrap: wrap;
    }
  }

  &__item {
    padding-bottom: 16px;
    @media (min-width: $breakpoint-medium) {
      width: 50%;
      box-sizing: border-box;
      padding: 0 0 32px 24px;
    }
  }

  &__sub-title {
    margin-bottom: 16px;
    @media (min-width: $breakpoint-medium) {
      margin-bottom: 24px;
    }
  }
}
