@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.instagram-wall {
  /*padding: 30px 0;*/
  position: relative;
  overflow: hidden;
  @media (min-width: $breakpoint-medium) {
    /*padding: 120px 0;*/
  }

  &__title {
    margin-bottom: 24px;
    @media (min-width: $breakpoint-medium) {
      margin-bottom: 50px;
    }
  }

  .big-card {
    height: 300px;
    outline: none;
    @media (min-width: $breakpoint-medium) {
      height: 424px;
    }
    > :first-child {
      width: calc(100% - 24px);
      height: calc(100% - 24px);
    }
  }
  .instagram-wall__collection {
    display: flex !important;
    flex-wrap: wrap;
    height: 300px;
    outline: none;
    @media (min-width: $breakpoint-medium) {
      height: 424px;
    }
    > :nth-child(1) {
      margin-right: 24px;
    }
    > :nth-child(-n + 2) {
      height: calc(50% - 24px);
      width: calc(50% - 24px);
    }
    > :nth-child(3) {
      width: calc(100% - 24px);
      height: calc(50% - 24px);
    }
  }
}

.instagram__slick-arrow {
  top: -18%;

  &-prev {
    left: auto !important;
    right: 70px !important;
  }
  &-next {
    left: auto !important;
    right: 5px !important;
  }
  @media (max-width: $breakpoint-small-max) {
    top: -15%;
  }
}

.instagram__slick-slider {
  > :nth-child(2) {
    overflow: visible !important;
  }
  @media (max-width: $breakpoint-small) {
    > :nth-child(1) {
      overflow: visible !important;
    }
  }
}
