@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.cta-new {
  padding: 40px 0;
  justify-content: center;
  position: relative;
  @media (min-width: $breakpoint-medium) {
    padding-bottom: 60px;
    height: 416px;
  }
  &__foreground {
    max-width: $container-max-width;
    margin: auto;
    display: flex;

    &--left {
      justify-content: flex-start;
    }

    &--center {
      justify-content: center;
    }

    &--right {
      justify-content: flex-end;
    }

    &--content-left {
      text-align: left;
    }

    &--content-center {
      text-align: center;
      @media (max-width: $breakpoint-small) {
        text-align: left;
      }
    }

    &--content-right {
      text-align: right;
    }

    z-index: 1;
    position: relative;
    @media (min-width: $breakpoint-medium) {
      display: flex;
      align-items: center;
      height: inherit;
      .uk-container {
        width: 100%;
      }
    }
  }
  &__background {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 0;
  }
  &__title {
    margin-bottom: 16px;
  }
  &__body {
    @media (min-width: $breakpoint-small) {
      max-width: 70%;
    }
    @media (min-width: $breakpoint-medium) {
      max-width: 60%;
    }
  }
  &__description {
    margin-bottom: 24px;
  }
  &__button-list {
    margin: 0 0 -16px;
    padding: 0;
    list-style: none;

    &--content-left {
      justify-content: flex-start;
    }

    &--content-center {
      justify-content: center;
      @media (max-width: $breakpoint-small) {
        justify-content: flex-start;
      }
    }

    &--content-right {
      justify-content: flex-end;
    }

    > li {
      padding: 0 0 16px;
    }
    @media (min-width: $breakpoint-small) {
      display: flex;
      align-items: center;
      margin: 0 -8px;
      > li {
        padding: 0 8px;
      }
    }
  }
  &__decor {
    position: absolute;
    bottom: -1px;
    left: 0;
    right: 0;
    text-align: center;
    svg {
      width: 100%;
    }
    @media (max-width: $breakpoint-small-max) {
      display: none;
    }
    @media (min-width: $breakpoint-large) {
      svg {
        width: 100%;
      }
    }
  }
}
