@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.layout-two-columns {
  background: #f5fafa;
  padding: 32px 0;
  position: relative;
  @media (min-width: $breakpoint-medium) {
    padding: 72px 0;
  }
  &__foreground {
    position: relative;
  }
  &--decor-top {
    padding-top: 40px;
    @media (min-width: $breakpoint-small) {
      padding-top: 72px;
    }
    @media (min-width: $breakpoint-medium) {
      padding-top: 120px;
    }
  }
  &--decor-bottom {
    padding-bottom: 40px;
    @media (min-width: $breakpoint-small) {
      padding-bottom: 72px;
    }
    @media (min-width: $breakpoint-medium) {
      padding-bottom: 120px;
    }
  }
}
.product-overview {
  &__header {
    margin-bottom: 56px;
  }
  &__wrap {
    margin-bottom: -16px;
  }
  &__block {
    margin-bottom: 16px;
    .slick-slide {
      @media (min-width: $breakpoint-small) {
        > div {
          min-height: 284px;
        }
      }
    }

    .card--product {
      @media (max-width: $breakpoint-xsmall-max) {
        border-bottom: solid 1px $global-border;
      }
    }
  }
  .slick-slider {
    > .slick-list {
      padding-top: 3px;
      padding-bottom: 3px;
    }
  }
}
.empty-state {
  height: 70vh;
  display: flex;
  align-items: center;
  margin: auto;
  width: 50%;
}
