@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.responsive {
  @media (max-width: $breakpoint-xsmall-max) {
    display: none;
  }
}
.sidebar {
  display: flex;
  flex-direction: column;
  @media (max-width: $breakpoint-xsmall-max) {
    margin-bottom: 8px !important;
  }

  > * {
    &:not(:first-child) {
      margin-top: 24px;
      @media (max-width: $breakpoint-xsmall-max) {
        margin-top: 8px;
      }
    }
  }

  &__block {
    margin-top: 32px;
    @media (max-width: $breakpoint-xsmall-max) {
      order: 0;
      margin-top: 16px;
    }
  }
  &__search {
    @media (max-width: $breakpoint-xsmall-max) {
      order: 1;
    }
  }
  .uk-accordion {
    margin: 0 0 8px 0;
    &-title {
      background-color: $global-inverse-color;
      border: 1px solid $global-border;
      padding: 10px 16px;
      display: flex;
      justify-content: space-between;
      border-radius: 4px;
      color: $global-color;
      font-size: 16px;
      height: 45px;
      box-sizing: border-box;
      align-items: center;
      &:hover,
      &:focus {
        color: $global-color;
      }
    }
    &-content {
      margin: 0 0 20px 0;
      @media (min-width: $breakpoint-small) {
        display: block !important;
      }
    }
  }
  &--modify {
    @media (max-width: $breakpoint-xsmall-max) {
      margin-bottom: 24px !important;
    }
    .sidebar {
      &__search {
        order: 0;
      }
    }
    > * {
      &:not(:first-child) {
        margin-top: 16px;
        @media (max-width: $breakpoint-xsmall-max) {
          margin-top: 8px;
        }
      }
    }
  }
}
