@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.newsletter {
  padding: 72px 0;
  position: relative;
  background: #faf0f0 url("../../../assets/images/pattern.jpg");
  background-size: 500px 499px;
  &__container {
    width: 900px;
    margin: auto;
    @media (max-width: $breakpoint-small-max) {
      margin: 0 16px;
      width: auto;
    }
  }
  @media (max-width: $breakpoint-small-max) {
    padding: 56px 0;
  }

  &__foreground {
    position: relative;
    z-index: 1;
  }
  &__body {
    display: flex;
    align-items: center;
  }
  &__description {
    margin: 22px 0;
  }
  &__image {
    max-width: 24%;
    margin-right: 32px;
    @media (max-width: $breakpoint-small-max) {
      display: none;
    }
  }
  &__inner {
    flex: 1 1 auto;
  }
  &__title {
    margin: 10px 0;
  }
  &__wrap {
    @media (min-width: $breakpoint-medium) {
      max-width: 90%;
    }
  }
}
.uk-container {
  &-medium {
  }
}
