@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.info-header {
  color: #333;
  font-size: 16px;
  padding-top: 72px;
  background-size: 500px 499px;
  position: relative;
  @media (min-width: $breakpoint-medium) {
    min-height: 188px;
    font-size: 20px;
  }
  &__button {
    margin: 12px 0;
    line-height: 1.29;
  }
  &__foreground {
    position: relative;
    width: 100%;
    justify-content: space-between;
    display: contents;
    &--left {
      flex-direction: row-reverse;
      justify-content: space-evenly;
    }
    @media (min-width: $breakpoint-xsmall-max) {
      display: inline-flex;
    }
  }
  &__body-text {
    margin-bottom: 0px;
  }
  &__back {
    display: inline-flex;
    align-items: center;
    color: #7f7f7f;
    font-size: 14px;
    line-height: 1;
    margin-bottom: 12px;
    @media (min-width: $breakpoint-small) {
      margin-bottom: 6px;
    }
    path {
      fill: #7f7f7f;
    }
    &:hover {
      text-decoration: none;
      color: $gray;
      path {
        fill: $gray;
      }
    }

    svg {
      margin-right: 10px;
    }
  }
  &__body {
    flex-grow: 0;

    @media (min-width: $breakpoint-small) {
      width: 50%;
      max-width: 50%;
    }
    .button {
      width: auto;
    }
  }
  &__image {
    max-width: 500px;
    margin: 0;
    width: auto;
    clip-path: polygon(
      0.4% 0.5%,
      10% 0%,
      14% 0%,
      24% 0.5%,
      26% 0.5%,
      35% 0%,
      39% 0%,
      49% 0.5%,
      51% 0.5%,
      60% 0%,
      64% 0%,
      74% 0.5%,
      76% 0.5%,
      85% 0%,
      89% 0%,
      98.2% 0.5%,
      99.4% 0.5%,
      100% 15%,
      99.6% 33%,
      100% 47%,
      99.6% 66%,
      99.8% 85%,
      99.4% 99.6%,
      89% 100%,
      85% 100%,
      76% 99.5%,
      74% 99.5%,
      64% 100%,
      60% 100%,
      51% 99.5%,
      49% 99.5%,
      39% 100%,
      35% 100%,
      24% 99.5%,
      26% 99.5%,
      14% 100%,
      10% 100%,
      0.4% 99.5%,
      0 90%,
      0.4% 66%,
      0 47%,
      0.4% 33%,
      0% 15%,
      0.4% 0.4%
    );
    @media (max-width: $breakpoint-small) {
      max-width: 4000px;
      width: 100%;
      padding: 0;
      margin: auto;
      margin-right: 16px;
      text-align: center;
      img {
        width: 100%;
        object-fit: cover;
        margin-bottom: -40px;
      }
    }
  }

  &--center {
    @media (min-width: $breakpoint-small) {
      text-align: center;
    }
    .info-header {
      &__body {
        margin: 0 auto;
        @media (min-width: $breakpoint-small) {
          width: 70%;
          max-width: 70%;
        }
        @media (min-width: $breakpoint-medium) {
          width: 55%;
          max-width: 55%;
        }
      }
    }
  }
}
