@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.recipe-head-card {
  position: relative;
  padding: 0 0 40px;
  @media (min-width: $breakpoint-medium) {
    padding-bottom: 72px;
  }
  &__foreground {
    position: relative;
  }
  &__header {
    position: relative;
    padding: 40px 0;
    @media (min-width: $breakpoint-medium) {
      padding: 72px 0;
    }
  }
  &__header-background {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background: url("../../../../assets/images/pattern.jpg");
    background-size: 500px 499px;
    height: 350px;
    @media (min-width: $breakpoint-medium) {
      height: 25%;
    }
    svg {
      position: absolute;
      bottom: 0px;
      left: 0;
      right: 0;
    }
  }
  &__info {
    position: relative;
    margin: 0 -16px;
    @media (min-width: $breakpoint-medium) {
      clip-path: polygon(
        0.4% 0.5%,
        10% 0%,
        14% 0%,
        24% 0.5%,
        26% 0.5%,
        35% 0%,
        39% 0%,
        49% 0.5%,
        51% 0.5%,
        60% 0%,
        64% 0%,
        74% 0.5%,
        76% 0.5%,
        85% 0%,
        89% 0%,
        98.2% 0.5%,
        99.4% 0.5%,
        100% 15%,
        99.6% 33%,
        100% 47%,
        99.6% 66%,
        99.8% 85%,
        99.4% 99.6%,
        89% 100%,
        85% 100%,
        76% 99.5%,
        74% 99.5%,
        64% 100%,
        60% 100%,
        51% 99.5%,
        49% 99.5%,
        39% 100%,
        35% 100%,
        24% 99.5%,
        26% 99.5%,
        14% 100%,
        10% 100%,
        0.4% 99.5%,
        0 90%,
        0.4% 66%,
        0 47%,
        0.4% 33%,
        0% 15%,
        0.4% 0.4%
      );
      margin: 0;
    }
    @media (min-width: $breakpoint-medium) {
      height: 420px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info-box {
    padding: 1px;
    clip-path: polygon(
      0.4% 0.5%,
      10% 0%,
      14% 0%,
      24% 0.5%,
      26% 0.5%,
      35% 0%,
      39% 0%,
      49% 0.5%,
      51% 0.5%,
      60% 0%,
      64% 0%,
      74% 0.5%,
      76% 0.5%,
      85% 0%,
      89% 0%,
      98.2% 0.5%,
      99.4% 0.5%,
      100% 15%,
      99.6% 33%,
      100% 47%,
      99.6% 66%,
      99.8% 85%,
      99.4% 99.6%,
      89% 100%,
      85% 100%,
      76% 99.5%,
      74% 99.5%,
      64% 100%,
      60% 100%,
      51% 99.5%,
      49% 99.5%,
      39% 100%,
      35% 100%,
      24% 99.5%,
      26% 99.5%,
      14% 100%,
      10% 100%,
      0.4% 99.5%,
      0 90%,
      0.4% 66%,
      0 47%,
      0.4% 33%,
      0% 15%,
      0.4% 0.4%
    );
    background: rgba(0, 0, 0, 0.1);
    margin: -40px 16px 0;
    @media (min-width: $breakpoint-medium) {
      width: 300px;
      margin: 0;
      position: absolute;
      left: 24px;
      top: 24px;
    }
  }
  &__info-box-holder {
    position: relative;
    padding: 24px;
    box-sizing: border-box;
    background: $global-background;
    clip-path: polygon(
      0.4% 0.5%,
      10% 0%,
      14% 0%,
      24% 0.5%,
      26% 0.5%,
      35% 0%,
      39% 0%,
      49% 0.5%,
      51% 0.5%,
      60% 0%,
      64% 0%,
      74% 0.5%,
      76% 0.5%,
      85% 0%,
      89% 0%,
      98.2% 0.5%,
      99.4% 0.5%,
      100% 15%,
      99.6% 33%,
      100% 47%,
      99.6% 66%,
      99.8% 85%,
      99.4% 99.6%,
      89% 100%,
      85% 100%,
      76% 99.5%,
      74% 99.5%,
      64% 100%,
      60% 100%,
      51% 99.5%,
      49% 99.5%,
      39% 100%,
      35% 100%,
      24% 99.5%,
      26% 99.5%,
      14% 100%,
      10% 100%,
      0.4% 99.5%,
      0 90%,
      0.4% 66%,
      0 47%,
      0.4% 33%,
      0% 15%,
      0.4% 0.4%
    );
  }
  &__info-list {
    padding: 0;
    margin: 0 0 24px;
    list-style: none;
    @media (max-width: $breakpoint-xsmall-max) {
      column-count: 2;
      column-gap: 8px;
    }
    li {
      margin-bottom: 12px;
      a {
        line-height: 1;
        &:hover {
          text-decoration: none;
        }
      }
    }
    svg {
      margin-bottom: 4px;
      margin-right: 8px;
      path {
        fill: currentColor;
      }
    }
  }
}
