@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
@import "../../../styles/curve.scss";

.card {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  clip-path: $curve;

  &:hover {
    text-decoration: none;
  }

  &--featured-slider,
  &--category {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    padding: 24px;
    text-align: left;
    flex: 1 1 auto;
    background: url("../../../../assets/images/image-mask.svg") no-repeat 50% 50%;
    background-size: cover;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.04), 0 0px 2px 0 rgba(0, 0, 0, 0.09);
    box-sizing: border-box;

    @media (min-width: $breakpoint-small) {
      width: 100%;
      height: 100%;
    }

    @media (max-width: $breakpoint-xsmall-max) {
      box-shadow: none;
    }

    &.card--modify {
      .card {
        &__bottom {
          @media (max-width: $breakpoint-xsmall-max) {
            display: none;
          }
        }
      }
    }

    .card {
      &__title {
        margin: 0;
      }

      &__head {
        flex: 1 1 auto;
      }

      &__body {
        display: flex;
        flex-direction: column;
        height: 100%;
      }

      &__bottom {
        .button {
          justify-content: left;
        }
      }
    }
  }
}
