@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.sidebar-menu {
  background: $global-background;
  border: 1px solid $global-border;
  margin-bottom: 8px;
  padding: 16px;
  font-size: 16px;
  color: $gray;
  list-style: none !important;
  @media (min-width: $breakpoint-medium) {
    padding: 24px;
    margin-bottom: 16px;
  }
  &__title {
    @extend .uk-h4 !optional;
    margin-bottom: 27px;
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style: none !important;
  }
  .sidebar-menu__list--check {
    .sidebar-menu {
      &__item {
        > a {
          align-items: flex-start;
        }
        svg {
          path {
            fill: $brand-primary;
          }
        }
      }
    }
  }
  &__select {
    font-size: 16px;
    line-height: 24px;
    font-family: $global-font-family;
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    height: 48px;
    border-radius: 4px;
    border: 1px solid $global-border;
    display: block;
    background-color: $global-inverse-color;
    &::-webkit-input-placeholder {
      color: $gray-light;
    }
    &::-moz-placeholder {
      color: $gray-light;
    }
    &:-ms-input-placeholder {
      color: $gray-light;
    }
    &:focus {
      outline: none;
      border-color: #7f7f7f;
    }
  }
}
