@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.product-overview {
  background: #f5fafa;
  &__header {
    margin-bottom: 56px;
  }

  &__wrap {
    margin-bottom: -16px;
  }

  &__block {
    margin-bottom: 16px;

    .slick-slide {
      @media (min-width: $breakpoint-small) {
        > div {
          min-height: 284px;
        }
      }
    }

    .card--product {
      @media (max-width: $breakpoint-xsmall-max) {
        border-bottom: solid 1px $global-border;
      }
    }
  }

  .slick-slider {
    > .slick-list {
      padding-top: 3px;
      padding-bottom: 3px;
    }
  }
}
.empty-state {
  height: 70vh;
  width: 70vh;
  display: flex;
  align-items: center;
  margin: auto;
}
