@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
$clip-path: polygon(
  2px 2px,
  10% 0%,
  14% 1px,
  24% 2px,
  26% 2px,
  35% 0%,
  39% 1px,
  49% 2px,
  51% 2px,
  60% 0%,
  64% 1px,
  74% 2px,
  76% 2px,
  85% 0%,
  89% 1px,
  98.2% 2px,
  calc(100% - 2px) 3px,
  100% 15%,
  calc(100% - 1px) 33%,
  100% 47%,
  calc(100% - 1px) 66%,
  99.8% 85%,
  calc(100% - 1px) calc(100% - 1px),
  89% 100%,
  85% 100%,
  76% calc(100% - 3px),
  74% calc(100% - 3px),
  64% 100%,
  60% 100%,
  51% calc(100% - 3px),
  49% calc(100% - 3px),
  39% 100%,
  35% 100%,
  24% calc(100% - 3px),
  26% calc(100% - 3px),
  14% 100%,
  10% 100%,
  1px calc(100% - 3px),
  0 90%,
  1px 66%,
  0 47%,
  1px 33%,
  0% 15%,
  1px 1px
);

.container {
  clip-path: $clip-path;
  padding: 1px;
  background: rgba(0, 0, 0, 0.05);
}
.card {
  clip-path: $clip-path;
  text-decoration: none;
  display: flex;
  background-color: white;
  flex-direction: column;
  &:hover {
    text-decoration: none;
  }
  &__user {
    display: flex;
    flex-direction: column;
    @media (max-width: $breakpoint-xsmall-max) {
      flex-direction: row;
      align-items: center;
      position: relative;
    }
    .card {
      &__body {
        padding: 24px;
        position: relative;
        @media (max-width: $breakpoint-xsmall-max) {
          padding: 16px 40px 16px 16px;
          flex: 1 1 auto;
          position: static;
        }
      }
      &__title {
        margin: -2% 0 -8%;
      }
      &__position {
        font-size: 16px;
        margin-top: 10%;
        color: $lighter;
        @media (max-width: $breakpoint-xsmall-max) {
          font-size: 14px;
        }
      }
      &__media {
        @media (max-width: $breakpoint-xsmall-max) {
          width: 27%;
          flex-shrink: 0;
        }
      }
      &__image {
        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }
      }
      &__social {
        position: absolute;
        background-image: url(../../../assets/onze-mensen/linked-in.svg);
        width: 24px;
        height: 24px;
        background-size: contain;
        left: 24px;
        top: -12px;
        background-color: $global-inverse-color;
        border-radius: 50%;
        overflow: hidden;
        @media (max-width: $breakpoint-xsmall-max) {
          bottom: 14px;
          right: 14px;
          left: auto;
          top: auto;
          width: 18px;
          height: 18px;
        }
      }
    }
  }
}
