@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.topCurve {
  position: absolute;
  height: 35%;
  z-index: 98;
  top: 0;
  right: 0;
  left: 0;
}
.decor-curve {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 97;
  &--top {
    top: -1px;
    &--left {
      transform: scale(-1, -1);
    }
    &--right {
      transform: scale(1, -1);
    }
  }
  &--middle {
    top: 35%;
    &--left {
      transform: scale(-1, -1);
    }
    &--right {
      transform: scale(1, -1);
    }
  }

  &--bottom {
    bottom: -1px;
    &--left {
      transform: scale(-1, 1);
    }
    &--right {
      transform: scale(1, 1);
    }
  }
}
