@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.widget-items {
  &__title {
    margin: 0 0 8px 0;
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    > li {
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
  &__card {
    display: flex;
    align-items: center;
    color: $gray;
    font-size: 14px;
    &:hover {
      text-decoration: none;
    }
    &-image {
      flex-shrink: 0;
      padding-right: 16px;
      width: 64px;
      height: 42px;
    }
  }
  &__bottom {
    margin-top: 16px;
  }
}
