@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
$curve: polygon(
  0.4% 0.5%,
  10% 0%,
  14% 0%,
  24% 0.5%,
  26% 0.5%,
  35% 0%,
  39% 0%,
  49% 0.5%,
  51% 0.5%,
  60% 0%,
  64% 0%,
  74% 0.5%,
  76% 0.5%,
  85% 0%,
  89% 0%,
  98.2% 0.5%,
  99.4% 0.5%,
  100% 15%,
  99.6% 33%,
  100% 47%,
  99.6% 66%,
  99.8% 85%,
  99.4% 99.6%,
  89% 100%,
  85% 100%,
  76% 99.5%,
  74% 99.5%,
  64% 100%,
  60% 100%,
  51% 99.5%,
  49% 99.5%,
  39% 100%,
  35% 100%,
  24% 99.5%,
  26% 99.5%,
  14% 100%,
  10% 100%,
  0.4% 99.5%,
  0 90%,
  0.4% 66%,
  0 47%,
  0.4% 33%,
  0% 15%,
  0.4% 0.4%
);
.simple-card {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  font-size: 10px;

  &:hover {
    text-decoration: none;
  }
  &--container {
    box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.04);
    position: relative;
    .simple-card {
      &__media {
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        top: 0;
        display: flex;
        clip-path: $curve;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &__image {
        width: 100%;
        height: 100%;
      }
      &__body {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 0px 24px;
        box-sizing: border-box;
        background: #fff;
        display: flex;
        align-items: center;
        height: 25%;
        @media (max-width: $breakpoint-small-max) {
          height: 30%;
        }
      }
      &__title {
        color: $gray-dark;
        font-size: 15px;
        font-weight: 600;
        max-height: 35px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        line-height: 18px; /* fallback */
        -webkit-line-clamp: 2; /* number of lines to show */
        /* autoprefixer: off */
        -webkit-box-orient: vertical;
      }
      &__icon {
        position: absolute;
        left: 16px;
        top: 16px;
        z-index: 2;
        fill: $brand-primary;
      }
    }
  }
}
