@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
.faq-overview {
  padding: 40px 0;
  position: relative;
  @media (min-width: $breakpoint-medium) {
    padding: 72px 0;
  }
  &__sidebar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    &__search {
      -webkit-order: 0;
      order: 0;
    }
    &__block {
      margin-top: 24px;
    }
  }
}
.empty-state {
   margin-top: 12px;
  > div {
    margin-top: 26px;
    display: flex;
    flex-direction: column;
    > a{
      margin-top:4px
    }
  }
}
