@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
@import "../../../styles/curve.scss";

.card {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  clip-path: $curve;

  &:hover {
    text-decoration: none;
  }

  &--featured-slider,
  &--product {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    padding: 32px 16px;
    text-align: center;
    background: url("../../../../assets/images/image-mask.svg") no-repeat 50% 50%;
    background-size: cover;
    box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.04), 0 0px 1px 0 rgba(0, 0, 0, 0.09);

    @media (max-width: $breakpoint-small-max) {
      padding: 24px 16px;
    }

    @media (max-width: $breakpoint-xsmall-max) {
      flex-direction: row;
      padding: 16px;
      align-items: center;
      text-align: left;
      box-shadow: none;
    }

    .card {
      &__image {
        overflow: hidden;
        height: 130px;
        margin-bottom: 25px;

        @media (max-width: $breakpoint-small-max) {
          margin-bottom: 16px;
          height: 72px;
          width: 72px;
          margin: auto;
        }

        @media (max-width: $breakpoint-xsmall-max) {
          margin: 0;
          height: auto;
        }

        img {
          width: auto;
          margin: 0 auto;
        }
      }

      &__media {
        @media (max-width: $breakpoint-xsmall-max) {
          margin: 0 16px 0 0;
          flex: 0 0 auto;
          width: 30%;
        }
      }

      &__body {
        padding: 0;
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        width: 100%;
      }

      &__title {
        font-size: 14px;
        line-height: 1.57;
        text-transform: uppercase;
        font-weight: 600;
        color: $gray-dark;
      }
    }
  }

  &--product {
    padding: 16px;
    min-height: inherit;
    box-sizing: border-box;

    .card {
      &__head {
        display: flex;
        height: 100%;
        flex-direction: column;
        flex: 1 1 auto;
      }

      &__title {
        flex: 1 1 auto;
        text-transform: capitalize;
        margin-bottom: 24px;

        @media (max-width: $breakpoint-xsmall-max) {
          margin-bottom: 6px;
        }
      }

      &__content {
        font-size: 12px;
        color: $gray-light;
      }
    }
  }
}
