@import "src/shared/styles/variables.scss";
                 @import "src/shared/styles/global-style.scss";
$curve: polygon(
  0.4% 0.5%,
  10% 0%,
  14% 0%,
  24% 0.5%,
  26% 0.5%,
  35% 0%,
  39% 0%,
  49% 0.5%,
  51% 0.5%,
  60% 0%,
  64% 0%,
  74% 0.5%,
  76% 0.5%,
  85% 0%,
  89% 0%,
  98.2% 0.5%,
  99.4% 0.5%,
  100% 15%,
  99.6% 33%,
  100% 47%,
  99.6% 66%,
  99.8% 85%,
  99.4% 99.6%,
  89% 100%,
  85% 100%,
  76% 99.5%,
  74% 99.5%,
  64% 100%,
  60% 100%,
  51% 99.5%,
  49% 99.5%,
  39% 100%,
  35% 100%,
  24% 99.5%,
  26% 99.5%,
  14% 100%,
  10% 100%,
  0.4% 99.5%,
  0 90%,
  0.4% 66%,
  0 47%,
  0.4% 33%,
  0% 15%,
  0.4% 0.4%
);

.welcome {
  h1 {
    font-size: 55px;
  }
  @media (max-width: $breakpoint-xsmall-max) {
    h1 {
      font-size: 40px;
    }
  }

  position: relative;

  &__background {
    height: 60vh;
    position: absolute;
    width: 100%;
    @media (max-width: $breakpoint-small-max) {
      background-size: 100% 100%, cover !important;
      background-position: left top, 50% 50% !important;
      height: 334px;
    }
  }

  &__background-pattern {
    position: absolute;
    bottom: 0;
  }
  &__decor {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    fill: currentColor;
    transform: scale(-1, 1);
    z-index: 0;
  }

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 125px;
    z-index: 1;
    position: relative;
    @media (max-width: $breakpoint-small-max) {
      margin-top: 40px;
    }
  }
  &__text {
    width: 50%;
    @media (max-width: $breakpoint-small-max) {
      width: 60%;
      font-size: 18px;
    }

    @media (max-width: $breakpoint-xsmall-max) {
      width: 90%;
    }
  }

  &__card-container {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-top: 146px;
    @media (max-width: $breakpoint-medium-max) {
      margin-top: 50px;
    }
    @media (max-width: $breakpoint-small-max) {
      margin-top: 20px;
      flex-direction: column;
    }
  }

  &__medium-card-container {
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    flex: 1 1 66%;

    justify-content: space-between;

    @media (max-width: 480px) {
      flex-direction: column;
    }
  }
  &__card {
    position: relative;
    flex: 1 1 50%;
    margin-right: 35px;

    > img {
      max-height: 333px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      clip-path: $curve;
    }
    @media (max-width: $breakpoint-small-max) {
      &:last-of-type {
        margin-right: 0px;
        display: flex;
        justify-content: flex-end;
      }
    }

    @media (max-width: 480px) {
      margin: 0px;
      > img {
        display: none;
      }
    }
  }

  &__link {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    bottom: -4%;
    padding-left: 10px;
    padding-right: 10px;
    width: 80%;
    height: 80px;
    box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.04);
    border: solid 1px #eceaea;
    background-color: #ffffff;
    left: 0;
    right: 0;
    margin: auto;
    clip-path: $curve;
    &:link,
    &:visited {
      text-decoration: none;
    }

    > span:first-of-type {
      color: $gray-dark;
    }
    > img {
      display: none;
    }
    @media (max-width: $breakpoint-small-max) {
      height: 50px;
    }
    @media (max-width: $breakpoint-xsmall-max) {
      height: 40px;
    }

    @media (max-width: 480px) {
      height: auto;
      padding: 0px;
      margin-bottom: 8px;
      position: relative;
      width: 100%;
      > * {
        margin-right: 10px;
      }
      > img {
        display: block;
        width: 100%;
        max-width: 114px;
        height: auto;
        height: 64px;
        object-fit: cover;
      }

      > span {
        &:first-of-type {
          margin-right: auto;
          width: 200px;
          font-size: 16px;
        }
      }
    }
  }

  &__small-card-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1 1 33%;

    @media (max-width: $breakpoint-small-max) {
      flex-direction: row;
      margin-top: 45px;
    }

    @media (max-width: $breakpoint-xsmall-max) {
      margin-top: 35px;
      flex-direction: column;
      flex-basis: 100%;
    }
  }

  &__small-card {
    background-color: #ffffff;
    display: flex;
    align-items: center;
    border: solid 1px #eceaea;
    justify-content: space-between;
    align-self: baseline;
    flex: 0 1 32%;
    clip-path: $curve;
    text-decoration: none !important;
    &:not(:last-child) {
      margin-bottom: 10px;
    }
    > * {
      margin-right: 10px;
    }
    > img {
      width: 114px;
      object-fit: cover;
      @media (max-width: $breakpoint-small-max) {
        width: 38%;
      }
    }

    > span {
      &:first-of-type {
        color: $gray-dark;
        flex: 1 1 70%;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        line-height: 16px; /* fallback */
        max-height: 45px; /* fallback */
        -webkit-line-clamp: 3; /* number of lines to show */
        /* autoprefixer: off */
        -webkit-box-orient: vertical;
        @media (max-width: $breakpoint-medium-max) {
          font-size: 14px;
        }
        font-size: 16px;
      }
      &:last-of-type {
        flex: 1 1 10%;
        display: flex;
        justify-content: flex-end;
      }
    }

    @media (max-width: $breakpoint-xsmall-max) {
      margin-bottom: 8px;
      align-self: stretch;
      flex: 0 0 100%;
      > img {
        width: 114px;
        height: 64px;
      }
    }
  }
}
